"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/contexts/AuthContext"
import { Permission } from "@/types/permissions"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Separator } from "@/components/ui/separator"
import { AlertCircle, Loader2, UserPlus, Edit, Trash, Shield, RefreshCw } from "lucide-react"
import usuarioService, { type Usuario } from "@/services/usuarioService"
import { BackButton } from "@/components/ui/BackButton"
import { useToast } from "@/components/ui/use-toast"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"

export default function UsuariosPage() {
  const router = useRouter()
  const { hasPermission, user, refreshAuth } = useAuth()
  const { toast } = useToast()
  const [usuarios, setUsuarios] = useState<Usuario[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [usuarioParaExcluir, setUsuarioParaExcluir] = useState<Usuario | null>(null)
  const [excluindo, setExcluindo] = useState(false)
  const [dialogoAberto, setDialogoAberto] = useState(false)

  // Verificar se o usuário é administrador
  const isAdmin = user?.perfil === "ADMIN" || user?.perfil === "Administrador"

  // Verificar se o usuário tem permissão para visualizar usuários
  const canViewUsers = isAdmin || hasPermission(Permission.VIEW_USERS) || hasPermission("USUARIOS_VISUALIZAR")
  const canCreateUser = isAdmin || hasPermission(Permission.CREATE_USER) || hasPermission("USUARIOS_CRIAR")
  const canEditUser = isAdmin || hasPermission(Permission.EDIT_USER) || hasPermission("USUARIOS_EDITAR")
  const canDeleteUser = isAdmin || hasPermission(Permission.DELETE_USER) || hasPermission("USUARIOS_REMOVER")
  const canManagePermissions =
    isAdmin || hasPermission(Permission.MANAGE_PERMISSIONS) || hasPermission("USUARIOS_PERMISSOES")

  // Carregar usuários
  const carregarUsuarios = async () => {
    if (!canViewUsers) {
      setError("Você não tem permissão para visualizar usuários.")
      setLoading(false)
      return
    }

    setLoading(true)
    setError(null)

    try {
      console.log("Carregando lista de usuários...")
      const data = await usuarioService.listarUsuarios()
      console.log(`${data.length} usuários carregados`)
      setUsuarios(data)
      toast({
        title: "Usuários carregados",
        description: `${data.length} usuários encontrados.`,
      })
    } catch (err: any) {
      console.error("Erro ao carregar usuários:", err)

      // Verificar se é um erro de autenticação
      if (err.response && err.response.status === 401) {
        console.log("Erro de autenticação, tentando renovar token...")
        try {
          await refreshAuth()
          // Tentar novamente após renovar o token
          const data = await usuarioService.listarUsuarios()
          setUsuarios(data)
          return
        } catch (refreshError) {
          console.error("Falha ao renovar autenticação:", refreshError)
          setError("Sessão expirada. Por favor, faça login novamente.")
          router.push("/login")
          return
        }
      }

      setError("Erro ao carregar usuários. " + (err.message || "Tente novamente."))
      toast({
        title: "Erro",
        description: "Não foi possível carregar os usuários.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  // Carregar usuários ao montar o componente
  useEffect(() => {
    carregarUsuarios()
  }, [])

  // Função para confirmar exclusão de usuário
  const confirmarExclusao = (usuario: Usuario) => {
    console.log(`Solicitando confirmação para excluir usuário: ${usuario.id} - ${usuario.nome}`)
    setUsuarioParaExcluir(usuario)
    setDialogoAberto(true)
  }

  // Função para cancelar exclusão
  const cancelarExclusao = () => {
    console.log("Cancelando exclusão")
    setUsuarioParaExcluir(null)
    setDialogoAberto(false)
  }

  // Função para excluir usuário
  const excluirUsuario = async () => {
    if (!usuarioParaExcluir) {
      console.error("Tentativa de excluir sem usuário selecionado")
      return
    }

    setExcluindo(true)

    try {
      console.log(`Iniciando exclusão do usuário ${usuarioParaExcluir.id} (${usuarioParaExcluir.nome})`)

      // Armazenar ID e nome para uso após a exclusão
      const idExcluido = usuarioParaExcluir.id
      const nomeExcluido = usuarioParaExcluir.nome

      // Adicionar logs detalhados
      console.log("Chamando usuarioService.removerUsuario...")
      const resultado = await usuarioService.removerUsuario(idExcluido)
      console.log("Resultado da exclusão:", resultado)

      // Fechar o diálogo antes de mostrar o toast
      setUsuarioParaExcluir(null)
      setDialogoAberto(false)

      // Mostrar mensagem de sucesso
      toast({
        title: "Usuário excluído",
        description: `O usuário ${nomeExcluido} foi excluído com sucesso.`,
      })

      // Verificar se o usuário foi realmente excluído
      console.log("Verificando se o usuário foi realmente excluído...")
      try {
        await usuarioService.buscarUsuario(idExcluido)
        // Se chegou aqui, o usuário ainda existe
        console.error(`ALERTA: Usuário ${idExcluido} ainda existe após exclusão!`)
        toast({
          title: "Atenção",
          description:
            "O servidor confirmou a exclusão, mas o usuário ainda existe. Contate o administrador do sistema.",
          variant: "destructive",
        })
      } catch (err: any) {
        if (err.response && err.response.status === 404) {
          console.log(`Confirmado: Usuário ${idExcluido} não existe mais no sistema.`)
        } else {
          console.error("Erro ao verificar exclusão:", err)
        }
      }

      // Atualizar a lista de usuários
      console.log("Atualizando lista de usuários após exclusão")
      await carregarUsuarios()
    } catch (err: any) {
      console.error("Erro ao excluir usuário:", err)

      // Verificar se é um erro de autenticação
      if (err.response && err.response.status === 401) {
        console.log("Erro de autenticação, tentando renovar token...")
        try {
          await refreshAuth()
          // Tentar novamente após renovar o token
          if (usuarioParaExcluir) {
            await usuarioService.removerUsuario(usuarioParaExcluir.id)
            setUsuarioParaExcluir(null)
            setDialogoAberto(false)
            await carregarUsuarios()
            return
          }
        } catch (refreshError) {
          console.error("Falha ao renovar autenticação:", refreshError)
        }
      }

      toast({
        title: "Erro ao excluir usuário",
        description: err.message || "Não foi possível excluir o usuário. Tente novamente.",
        variant: "destructive",
      })
    } finally {
      setExcluindo(false)
    }
  }

  // Se o usuário não tem permissão para visualizar usuários, mostrar mensagem
  if (!canViewUsers) {
    return (
      <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">
        <div className="flex items-center gap-4 mb-4">
          <BackButton />
          <h2 className="text-3xl font-bold tracking-tight">Gerenciamento de Usuários</h2>
        </div>

        <div className="flex justify-center py-8">
          <Alert variant="destructive" className="max-w-md">
            <AlertCircle className="h-4 w-4" />
            <AlertDescription>Você não tem permissão para visualizar usuários.</AlertDescription>
          </Alert>
        </div>
      </div>
    )
  }

  const handleEditClick = (usuario: Usuario) => {
    router.push(`/usuarios/editar/${usuario.id}`)
  }

  const handleDeleteClick = (usuario: Usuario) => {
    confirmarExclusao(usuario)
  }

  // Função auxiliar para verificar se o usuário está ativo
  const isUsuarioAtivo = (usuario: Usuario): boolean => {
    // Verificar primeiro a propriedade ativo se existir
    if (typeof usuario.ativo === "boolean") {
      return usuario.ativo
    }
    // Caso contrário, verificar o status
    return usuario.status === "Ativo"
  }

  return (
    <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-4">
          <BackButton />
          <h2 className="text-3xl font-bold tracking-tight">Gerenciamento de Usuários</h2>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" onClick={carregarUsuarios} disabled={loading}>
            {loading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <RefreshCw className="mr-2 h-4 w-4" />}
            Atualizar
          </Button>
          {canCreateUser && (
            <Button onClick={() => router.push("/usuarios/novo")}>
              <UserPlus className="mr-2 h-4 w-4" />
              Novo Usuário
            </Button>
          )}
        </div>
      </div>

      {error && (
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <Card>
        <CardHeader>
          <CardTitle>Lista de Usuários</CardTitle>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div className="flex justify-center py-8">
              <Loader2 className="h-8 w-8 animate-spin text-primary" />
              <p className="ml-2">Carregando usuários...</p>
            </div>
          ) : usuarios.length === 0 ? (
            <div className="text-center py-8 text-muted-foreground">Nenhum usuário encontrado.</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>ID</TableHead>
                  <TableHead>Nome</TableHead>
                  <TableHead>Usuário</TableHead>
                  <TableHead>Email</TableHead>
                  <TableHead>Perfil</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead className="text-right">Ações</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {usuarios.map((usuario) => (
                  <TableRow key={usuario.id}>
                    <TableCell>{usuario.id}</TableCell>
                    <TableCell className="font-medium">{usuario.nome}</TableCell>
                    <TableCell>{usuario.username}</TableCell>
                    <TableCell>{usuario.email}</TableCell>
                    <TableCell>
                      {usuario.perfil === "ADMIN" || usuario.perfil === "Administrador" ? (
                        <Badge variant="default">Administrador</Badge>
                      ) : (
                        <Badge variant="outline">Operador</Badge>
                      )}
                    </TableCell>
                    <TableCell>
                      {isUsuarioAtivo(usuario) ? (
                        <Badge variant="outline" className="bg-green-100 text-green-800 hover:bg-green-200">
                          Ativo
                        </Badge>
                      ) : (
                        <Badge variant="outline" className="bg-red-100 text-red-800 hover:bg-red-200">
                          Inativo
                        </Badge>
                      )}
                    </TableCell>
                    <TableCell className="text-right">
                      <div className="flex justify-end gap-2">
                        {canEditUser && (
                          <Button size="sm" variant="ghost" onClick={() => handleEditClick(usuario)}>
                            <Edit className="h-4 w-4" />
                          </Button>
                        )}
                        {canDeleteUser && (
                          <Button size="sm" variant="ghost" onClick={() => handleDeleteClick(usuario)}>
                            <Trash className="h-4 w-4" />
                          </Button>
                        )}
                        {canManagePermissions && (
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={() => router.push(`/usuarios/permissoes/${usuario.id}`)}
                            title="Gerenciar Permissões"
                          >
                            <Shield className="h-4 w-4" />
                          </Button>
                        )}
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>

      {/* Diálogo de confirmação de exclusão */}
      <Dialog open={dialogoAberto} onOpenChange={(open) => !open && cancelarExclusao()}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Confirmar Exclusão</DialogTitle>
            <DialogDescription>
              Tem certeza que deseja excluir o usuário {usuarioParaExcluir?.nome}? Esta ação não pode ser desfeita.
            </DialogDescription>
          </DialogHeader>
          <Separator className="my-4" />
          <DialogFooter>
            <Button variant="outline" onClick={cancelarExclusao} disabled={excluindo}>
              Cancelar
            </Button>
            <Button variant="destructive" onClick={excluirUsuario} disabled={excluindo}>
              {excluindo ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
              {excluindo ? "Excluindo..." : "Excluir"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}
